import Link from "next/link";
import Image from "next/image";
import { FaFacebook, FaTwitter, FaInstagram } from "react-icons/fa";

const Footer = () => {
  return (
    <footer className="bg-blue-200 text-black py-10 px-4 md:px-16">
      {/* Top Section: Input + Buttons */}
     <div className="flex justify-center">
  <div className="relative w-full md:w-[700px]">
    <input
      type="email"
      placeholder="Enter your email"
      className="w-full border border-white  text-black px-4 py-3 pr-32 rounded-md focus:outline-none"
    />
    <button className="absolute top-1/2 -translate-y-1/2 right-2  border border-white text-black px-5 py-2 rounded-md hover:bg-yellow-400 transition-all duration-300">
      Subscribe
    </button>
  </div>
</div>


      {/* Bottom Footer Section */}
      <div className="border-t border-gray-700 pt-8 flex flex-col md:flex-row justify-between items-start text-sm">
        {/* Company Info */}
        <div className="mb-6 md:mb-0">
          <Link href="/">
                   <div >
                     <Image src="/img/logo5.png" alt="Logo" width={120} height={120} />
                   </div>
                 </Link>
          <p className="text-gray-400 max-w-sm">
            Buten is a modern eCommerce platform built with performance and simplicity in mind. Thank you for choosing us!
          </p>
        </div>

        {/* Quick Links */}
        <div className="mb-6 md:mb-0">
          <h2 className="text-lg font-semibold mb-2">Quick Links</h2>
          <ul className="space-y-1 text-gray-400">
            <li><a href="#">Home</a></li>
            <li><a href="#">Products</a></li>
            <li><a href="#">About Us</a></li>
            <li><a href="#">Contact</a></li>
          </ul>
        </div>

        {/* Contact Info */}
        <div>
          <h2 className="text-lg font-semibold mb-2">Contact</h2>
          <p className="text-gray-400">Email: support@buten.com</p>
          <p className="text-gray-400">Phone: +880 1234 567890</p>
          <div className="flex space-x-4 mt-2">
            <FaFacebook className="hover:text-blue-500 cursor-pointer" />
            <FaTwitter className="hover:text-blue-400 cursor-pointer" />
            <FaInstagram className="hover:text-pink-500 cursor-pointer" />
          </div>
        </div>
      </div>

      {/* Footer Bottom Note */}
      <div className="text-center text-gray-500 mt-10 text-xs">
        &copy; {new Date().getFullYear()} Buten. All rights reserved.
      </div>
    </footer>
  );
};

export default Footer;
